		main{
			box-sizing:border-box;
			background-color:#7fffd4;
			height:100vh;
			display:flex;
			flex-direction:column;
			justify-content:center;
			text-align:center;
			align-items:center;
		}
		main div{	
			margin:10px;
			border-bottom:2px solid gray;

		}
		main div a{
			text-decoration:none;
			color:darkred;
			font-size:24px;
			font-weight:400;
			letter-spacing:4px;
		}